/* Containers */
html,
body {
  width: 100%;
  background: #fbedca;
  color: white; }

body {
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  margin: 0px; }

