* a:hover {
  cursor: pointer; }

* {
  box-sizing: border-box; }

.links {
  color: #fbedca;
  text-decoration: underline; }

h1 {
  font-size: 2rem;
  font-weight: 900;
  line-height: 1.25em; }

h2 {
  font-size: 1.8rem;
  font-weight: 700;
  line-height: 1.25em; }

h3 {
  font-size: 1.4rem;
  font-weight: 600; }

h4 {
  font-size: 14px;
  line-height: 1.5em;
  font-weight: 400; }

p {
  font-size: 14px;
  line-height: 1.5em;
  font-weight: 500; }

#root {
  overflow-x: hidden; }

.hover-effect:hover {
  background-color: #ff1d8e;
  color: white; }

.live-card {
  background-color: gray; }

.active-live-card {
  background-color: darkblue; }

.scrollarea {
  width: 50%; }

@media only screen and (max-width: 780px) {
  .scrollarea {
    width: 100%; } }

